import {Modals} from './modals';

let modals;

// –ó–¥–µ—Å—å —Ä–µ–∞–ª–∏–∑–æ–≤–∞–Ω –ø—Ä–∏–º–µ—Ä –æ—Ç–∫—Ä—ã—Ç–∏—è –º–æ–¥–∞–ª–∫–∏ —á–µ—Ä–µ–∑ –∫–æ–ª–±—ç–∫ –∑–∞–∫—Ä—ã—Ç–∏—è
// const openModalInCloseCallback = (name, context = this) => {
//   context._enableScrolling = false;
//   context._setSettings('default');
//   modals.open(name);
// };

// closeCallback() {
//   openModalInCloseCallback('modal-5');
// },

const settings = {
  'default': {
    preventDefault: true,
    stopPlay: true,
    lockFocus: true,
    startFocus: true,
    focusBack: true,
    eventTimeout: 400,
    openCallback: false,
    closeCallback: false,
  },
};

const initModals = () => {
  const modalElements = document.querySelectorAll('.modal');
  modalElements.forEach((el) => {
    setTimeout(() => {
      el.classList.remove('modal--preload');
    }, 100);
  });
  modals = new Modals(settings);
  // –ò—Å–ø–æ–ª—å–∑—É–π—Ç–µ –≤ —Ä–∞–∑—Ä–∞–±–æ—Ç–∫–µ —ç–∫—Å–ø–æ—Ä—Ç–∏—Ä—É–µ–º—É—é –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é modals, window —Å–¥–µ–ª–∞–Ω –¥–ª—è –±—ç–∫—ç–Ω–¥–∞
  window.modals = modals;
};

export {modals, initModals};
